import React, {PropTypes} from 'react';
import KlassName from 'teapan/klassName';

class HeaderChatbar extends React.Component {
	constructor(props) {
		super(props);
	}

	renderChildren(){
		const props = this.props;
		const {
			flipped,
			className,
			children,
			...others
		} = props;

		const klassName = KlassName(
			'header-chatbar',
			'clearfix',
			className
		);

		return (
			<div
				{...others}
				className={klassName}
				style={{opacity: flipped ? 1 : 0,dispaly: flipped ? 'block' : 'none',}}
			>
				{children}
			</div>
		);
	}

	render() {
		return this.props.flipped ? this.renderChildren() : null;
	}
};

HeaderChatbar.propTypes = {
	flipped: PropTypes.bool,

	className: PropTypes.string,
	children: PropTypes.node,
};

HeaderChatbar.defaultProps = {
};

export default HeaderChatbar;